"use client"

import { createContext, useContext, useState, type ReactNode } from "react"
import { mockSOPs, type SOP, type Note, type ChatMessage } from "@/lib/mock-data"

interface SessionState {
  id: string
  title: string
  messages: ChatMessage[]
  notes: Note[]
  phase: "foundation" | "process" | "accountability" | "quality" | "finalization"
  createdAt: string
}

interface SOPContextType {
  sops: SOP[]
  currentSOP: SOP | null
  setCurrentSOP: (sop: SOP | null) => void
  addSOP: (sop: SOP) => void
  updateSOP: (id: string, updates: Partial<SOP>) => void
  deleteSOP: (id: string) => void
  addNote: (sopId: string, note: Note) => void
  updateNote: (sopId: string, noteId: string, updates: Partial<Note>) => void
  deleteNote: (sopId: string, noteId: string) => void
  addMessage: (sopId: string, message: ChatMessage) => void
  session: SessionState | null
  startNewSession: () => string
  updateSession: (updates: Partial<SessionState>) => void
  addSessionMessage: (message: ChatMessage) => void
  addSessionNote: (note: Note) => void
  updateSessionNote: (noteId: string, updates: Partial<Note>) => void
  deleteSessionNote: (noteId: string) => void
  finalizeSession: () => SOP | null
}

const SOPContext = createContext<SOPContextType | undefined>(undefined)

export function SOPProvider({ children }: { children: ReactNode }) {
  const [sops, setSops] = useState<SOP[]>(mockSOPs)
  const [currentSOP, setCurrentSOP] = useState<SOP | null>(null)
  const [session, setSession] = useState<SessionState | null>(null)

  const addSOP = (sop: SOP) => {
    setSops((prev) => [...prev, sop])
  }

  const updateSOP = (id: string, updates: Partial<SOP>) => {
    setSops((prev) =>
      prev.map((sop) => (sop.id === id ? { ...sop, ...updates, updatedAt: new Date().toISOString() } : sop)),
    )
    if (currentSOP?.id === id) {
      setCurrentSOP((prev) => (prev ? { ...prev, ...updates } : null))
    }
  }

  const deleteSOP = (id: string) => {
    setSops((prev) => prev.filter((sop) => sop.id !== id))
    if (currentSOP?.id === id) {
      setCurrentSOP(null)
    }
  }

  const addNote = (sopId: string, note: Note) => {
    updateSOP(sopId, {
      notes: [...(sops.find((s) => s.id === sopId)?.notes || []), note],
    })
  }

  const updateNote = (sopId: string, noteId: string, updates: Partial<Note>) => {
    const sop = sops.find((s) => s.id === sopId)
    if (sop) {
      updateSOP(sopId, {
        notes: sop.notes.map((n) => (n.id === noteId ? { ...n, ...updates } : n)),
      })
    }
  }

  const deleteNote = (sopId: string, noteId: string) => {
    const sop = sops.find((s) => s.id === sopId)
    if (sop) {
      updateSOP(sopId, {
        notes: sop.notes.filter((n) => n.id !== noteId),
      })
    }
  }

  const addMessage = (sopId: string, message: ChatMessage) => {
    const sop = sops.find((s) => s.id === sopId)
    if (sop) {
      updateSOP(sopId, {
        chatHistory: [...sop.chatHistory, message],
      })
    }
  }

  const startNewSession = () => {
    const newId = `session-${Date.now()}`
    setSession({
      id: newId,
      title: "New SOP",
      messages: [],
      notes: [],
      phase: "foundation",
      createdAt: new Date().toISOString(),
    })
    return newId
  }

  const updateSession = (updates: Partial<SessionState>) => {
    setSession((prev) => (prev ? { ...prev, ...updates } : null))
  }

  const addSessionMessage = (message: ChatMessage) => {
    setSession((prev) => (prev ? { ...prev, messages: [...prev.messages, message] } : null))
  }

  const addSessionNote = (note: Note) => {
    setSession((prev) => (prev ? { ...prev, notes: [...prev.notes, note] } : null))
  }

  const updateSessionNote = (noteId: string, updates: Partial<Note>) => {
    setSession((prev) =>
      prev ? { ...prev, notes: prev.notes.map((n) => (n.id === noteId ? { ...n, ...updates } : n)) } : null,
    )
  }

  const deleteSessionNote = (noteId: string) => {
    setSession((prev) => (prev ? { ...prev, notes: prev.notes.filter((n) => n.id !== noteId) } : null))
  }

  const finalizeSession = (): SOP | null => {
    if (!session) return null

    const newSOP: SOP = {
      id: session.id,
      title: session.title || "Untitled SOP",
      description: "",
      status: "draft",
      category: "General",
      createdAt: session.createdAt,
      updatedAt: new Date().toISOString(),
      notes: session.notes,
      chatHistory: session.messages,
      content: "",
    }

    addSOP(newSOP)
    setCurrentSOP(newSOP)
    return newSOP
  }

  return (
    <SOPContext.Provider
      value={{
        sops,
        currentSOP,
        setCurrentSOP,
        addSOP,
        updateSOP,
        deleteSOP,
        addNote,
        updateNote,
        deleteNote,
        addMessage,
        session,
        startNewSession,
        updateSession,
        addSessionMessage,
        addSessionNote,
        updateSessionNote,
        deleteSessionNote,
        finalizeSession,
      }}
    >
      {children}
    </SOPContext.Provider>
  )
}

export function useSOPContext() {
  const context = useContext(SOPContext)
  if (!context) {
    throw new Error("useSOPContext must be used within a SOPProvider")
  }
  return context
}
